@use "_mixins/breakpoints";
@use "colors";

/**** UCLA html5reset.css ****/

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: colors.$black;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: colors.$black;
  font-style: italic;
  font-weight: bold;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}


input,
select {
  vertical-align: middle;
}

/*  SECTIONS  ============================================================================= */
.ucla {
  clear: both;
  padding: 0;
  margin: 0;
}

/*  GROUPING  ============================================================================= */
.campus::before,
.campus::after {
  content: "";
  display: table;
}

.campus::after {
  clear: both;
}

.campus {
  zoom: 1; /* For IE 6/7 (trigger hasLayout) */
}

.ucla.campus {
  display: block;
  position: relative;
  max-width: 1176px;
  margin: 0 auto;
  padding: 0 24px;

  @media (min-width: breakpoints.breakpoint-min(lg)) {
    padding: 0 12px;
  }
}

/*  GRID COLUMN SETUP   ==================================================================== */
.col {
  display: block;
  float: left;
  margin: 0 0 0 1.6%;
}

.col:first-child { margin-left: 0; } /* all browsers except IE6 and lower */

/*  REMOVE MARGINS AS ALL GO FULL WIDTH AT 768 PIXELS */
@media only screen and (max-width: 768px) {
  .col {
    margin: 0;
  }
}


